<template>
    <div id="pageName">
        <h2>공지사항 -관리자-</h2>
    </div>   
    <hr class="my-4" />
    <!-- 공지사항 작성하기 버튼 -->
    <div id="noticeWrite">
        <button type="button" class="btn btn-primary" @click="moveWrite">공지사항 작성하기</button>
    </div>
    <!-- 공지사항 리스트 -->
    <div class="noticeList">
        <div v-for="(notice,i) in noticeList" :key="i">
            <div class="d-grid gap-2 col-10 mx-auto">
                <button class="btn btn-outline-dark" type="button" @click="moveDetails">{{ noticeList [i]}}</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'noticeList',
    data() {
        return {
            // 예시
            noticeList: [
                'Notice 1',
                'Notice 2',
                'Notice 3',
                'Notice 4',
                'Notice 5'
            ]
        }
    },
    methods: {
        moveDetails() {
            this.$router.push("/JY_A_NoticeDetails")
        },
        moveWrite() {
            this.$router.push("/JY_A_NoticeWrite")
        }
    }
}
</script>

<style>
    .btn-outline-dark {
        margin-top: 10px;
        height: 100px;
    }

</style>